@tailwind base;
@tailwind components;
@tailwind utilities;
.block-editor-writing-flow {
  @apply font-sans overflow-hidden p-0 font-normal;
  /* overwrite custom eidtor blocks */
  /* Components */
}
.block-editor-writing-flow .hide-for-editor {
  @apply hidden;
}
.block-editor-writing-flow .wp-block-group {
  border: dotted 1px grey;
}
.block-editor-writing-flow .wp-block,
.block-editor-writing-flow .wp-block-post-title {
  @apply px-0 container;
}
.block-editor-writing-flow .wp-block.has-background,
.block-editor-writing-flow .wp-block-post-title.has-background {
  @apply py-10 px-6;
}
.block-editor-writing-flow .wp-block {
  @apply py-0.5;
}
.block-editor-writing-flow .wp-block.wp-block-group.has-background {
  @apply my-0 px-6;
}
.block-editor-writing-flow .wp-block-acf-stroke {
  @apply max-w-full;
}
.block-editor-writing-flow .wp-block-acf-stroke svg {
  @apply w-full h-auto;
}
.block-editor-writing-flow .card {
  @apply mt-0;
}
.block-editor-writing-flow .swiper-wrapper {
  @apply grid grid-cols-4 gap-5;
}